<template>
  <div>
    <b-card class="invoice-padding form-item-section">
      <div
        ref="form"
        class="repeater-form"
        :style="{height: trHeight}"
      >
        <b-row
          v-for="(item, index) in invoiceData.items"
          :key="index"
          ref="row"
          class="pb-2"
        >
          <b-card class="border-info">
            <!-- Item Form -->
            <!-- ? This will be in loop => So consider below markup for single item -->
            <b-col cols="12">

              <!-- ? Flex to keep separate width for XIcon and SettingsIcon -->
              <!-- <div class="d-none d-lg-flex">
              <b-row class="flex-grow-1 px-1">

                <b-col
                  cols="12"
                  lg="5"
                >
                  Item
                </b-col>
                <b-col
                  cols="12"
                  lg="3"
                >
                  Cost
                </b-col>
                <b-col
                  cols="12"
                  lg="2"
                >
                  Qty
                </b-col>
                <b-col
                  cols="12"
                  lg="2"
                >
                  Priceaa
                </b-col>
              </b-row>
              <div class="form-item-action-col" />
            </div> -->

              <!-- Form Input Fields OR content inside bordered area  -->
              <!-- ? Flex to keep separate width for XIcon and SettingsIcon -->
              <div class="d-flex border rounded">
                <b-row class="flex-grow-1 p-2">
                  <!-- Single Item Form Headers -->
                  <b-col
                    cols="12"
                    lg="5"
                  >
                    <label class="d-inline d-lg-none">Item2</label>
                    <v-select
                      v-model="item.itemTitle"
                      :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                      :options="itemsOptions"
                      label="itemTitle"
                      :clearable="false"
                      class="mb-2 item-selector-title"
                      placeholder="Select Item"
                      @input="val => updateItemForm(index, val)"
                    />
                  </b-col>
                  <b-col
                    cols="12"
                    lg="3"
                  >
                    <label class="d-inline d-lg-none">Cost</label>
                    <b-form-input
                      v-model="item.cost"
                      type="number"
                      class="mb-2"
                    />
                  </b-col>
                  <b-col
                    cols="12"
                    lg="2"
                  >
                    <label class="d-inline d-lg-none">Qty</label>
                    <b-form-input
                      v-model="item.qty"
                      type="number"
                      class="mb-2"
                    />
                  </b-col>
                  <b-col
                    cols="12"
                    lg="2"
                  >
                    <label class="d-inline d-lg-none">Price</label>
                    <p class="mb-1">
                      ${{ item.cost * item.qty }}
                    </p>
                  </b-col>
                  <b-col
                    cols="12"
                    lg="5"
                  >
                    <label class="d-inline d-lg-none">Description</label>
                    <b-form-textarea
                      v-model="item.description"
                      class="mb-2 mb-lg-0"
                    />
                  </b-col>
                  <b-col>
                    <p class="mb-0">
                      Discount: 0% 0% 0%
                    </p>
                  </b-col>
                </b-row>
                <div class="d-flex flex-column justify-content-between border-left py-50 px-25">
                  <feather-icon
                    size="16"
                    icon="XIcon"
                    class="cursor-pointer"
                    @click="removeItem(index)"
                  />
                  <feather-icon
                    :id="`form-item-settings-icon-${index}`"
                    size="16"
                    icon="SettingsIcon"
                    class="cursor-pointer"
                  />

                  <!-- Setting Item Form -->
                  <b-popover
                    :ref="`form-item-settings-popover-${index}`"
                    :target="`form-item-settings-icon-${index}`"
                    triggers="click"
                    placement="lefttop"
                  >
                    <b-form @submit.prevent>
                      <b-row>

                        <!-- Field: Discount -->
                        <b-col cols="12">
                          <b-form-group
                            label="Discount(%)"
                            :label-for="`setting-item-${index}-discount`"
                          >
                            <b-form-input
                              :id="`setting-item-${index}-discount`"
                              :value="null"
                              type="number"
                            />
                          </b-form-group>
                        </b-col>

                        <!-- Field: Tax 1 -->
                        <b-col cols="6">
                          <b-form-group
                            label="Tax 1"
                            :label-for="`setting-item-${index}-tax-1`"
                          >
                            <v-select
                              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                              :value="'10%'"
                              :options="['0%', '1%', '10%', '14%', '18%']"
                              :input-id="`setting-item-${index}-tax-1`"
                              :clearable="false"
                            />
                          </b-form-group>
                        </b-col>

                        <!-- Field: Tax 2 -->
                        <b-col cols="6">
                          <b-form-group
                            label="Tax 2"
                            :label-for="`setting-item-${index}-tax-2`"
                          >
                            <v-select
                              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                              :value="'10%'"
                              :options="['0%', '1%', '10%', '14%', '18%']"
                              :input-id="`setting-item-${index}-tax-2`"
                              :clearable="false"
                            />
                          </b-form-group>
                        </b-col>

                        <b-col
                          cols="12"
                          class="d-flex justify-content-between mt-1"
                        >
                          <b-button
                            variant="outline-primary"
                            @click="() => {$refs[`form-item-settings-popover-${index}`][0].$emit('close')}"
                          >
                            Apply
                          </b-button>
                          <b-button
                            variant="outline-secondary"
                            @click="() => {$refs[`form-item-settings-popover-${index}`][0].$emit('close')}"
                          >
                            Cancel
                          </b-button>
                        </b-col>
                      </b-row>
                    </b-form>
                  </b-popover>
                </div>
              </div>
            </b-col>
          </b-card>
        </b-row>
      </div>
      <b-button
        v-ripple.400="'rgba(255, 255, 255, 0.15)'"
        size="sm"
        variant="primary"
        @click="addNewItemInItemForm"
      >
        Adicionar pregunta
      </b-button>
    </b-card>
  </div>
</template>

<script>
// import Logo from '@core/layouts/components/Logo.vue'
import { ref } from '@vue/composition-api'
import { heightTransition } from '@core/mixins/ui/transition'
import Ripple from 'vue-ripple-directive'
import {
  BRow, BCol, BCard, BButton, BForm, BFormGroup, BFormInput, BFormTextarea, BPopover, VBToggle,
} from 'bootstrap-vue'
import vSelect from 'vue-select'
// import flatPickr from 'vue-flatpickr-component'
// import store from '@/store'
// import invoiceStoreModule from '../invoiceStoreModule'
// import InvoiceSidebarAddNewCustomer from '../InvoiceSidebarAddNewCustomer.vue'

export default {
  components: {
    BRow,
    BCol,
    BCard,
    // BCardBody,
    BButton,
    // BCardText,
    BForm,
    BFormGroup,
    BFormInput,
    // BInputGroup,
    // BInputGroupPrepend,
    BFormTextarea,
    // BFormCheckbox,
    BPopover,
    // flatPickr,
    vSelect,
    // Logo,
    // InvoiceSidebarAddNewCustomer,
  },
  directives: {
    Ripple,
    'b-toggle': VBToggle,

  },
  mixins: [heightTransition],
  data() {
    return {
      itemsOptions: [
        {
          itemTitle: 'aaa',
          cost: 24,
          qty: 1,
          description: 'Designed UI kit & app pages.',
        },
      ],
    }
  },
  mounted() {
    this.initTrHeight()
  },
  created() {
    window.addEventListener('resize', this.initTrHeight)
  },
  destroyed() {
    window.removeEventListener('resize', this.initTrHeight)
  },
  methods: {
    addNewItemInItemForm() {
      this.$refs.form.style.overflow = 'hidden'
      this.invoiceData.items.push(JSON.parse(JSON.stringify(this.itemFormBlankItem)))
      this.$nextTick(() => {
        this.trAddHeight(this.$refs.row[0].offsetHeight)
        setTimeout(() => {
          this.$refs.form.style.overflow = null
        }, 350)
      })
    },
    removeItem(index) {
      this.invoiceData.items.splice(index, 1)
      this.trTrimHeight(this.$refs.row[0].offsetHeight)
    },
    initTrHeight() {
      this.trSetHeight(null)
      this.$nextTick(() => {
        this.trSetHeight(this.$refs.form.scrollHeight)
      })
    },
  },
  setup() {
    // const INVOICE_APP_STORE_MODULE_NAME = 'app-invoice'

    // // Register module
    // if (!store.hasModule(INVOICE_APP_STORE_MODULE_NAME)) store.registerModule(INVOICE_APP_STORE_MODULE_NAME, invoiceStoreModule)

    // // UnRegister on leave
    // onUnmounted(() => {
    //   if (store.hasModule(INVOICE_APP_STORE_MODULE_NAME)) store.unregisterModule(INVOICE_APP_STORE_MODULE_NAME)
    // })

    // const clients = ref([])
    // store.dispatch('app-invoice/fetchClients')
    //   .then(response => { clients.value = response.data })

    const itemFormBlankItem = {
      item: null,
      cost: 0,
      qty: 0,
      description: '',
    }

    const invoiceData = ref({
      id: 5037,
      client: null,

      // ? Set single Item in form for adding data
      items: [JSON.parse(JSON.stringify(itemFormBlankItem))],

      salesPerson: '',
      note: 'It was a pleasure working with you and your team. We hope you will keep us in mind for future freelance projects. Thank You!',
      paymentMethod: null,
    })

    // const updateItemForm = (index, val) => {
    //   const { cost, qty, description } = val
    //   invoiceData.value.items[index].cost = cost
    //   invoiceData.value.items[index].qty = qty
    //   invoiceData.value.items[index].description = description
    // }

    // const paymentMethods = [
    //   'Bank Account',
    //   'PayPal',
    //   'UPI Transfer',
    // ]

    return {
      invoiceData,
      // clients,
      // itemsOptions,
      // updateItemForm,
      itemFormBlankItem,
      // paymentMethods,
    }
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
@import '@core/scss/vue/libs/vue-flatpicker.scss';
.invoice-add-wrapper {
  .add-new-client-header {
    padding: $options-padding-y $options-padding-x;
      color: $success;

    &:hover {
      background-color: rgba($success, 0.12);
    }
  }
}
</style>

<style lang="scss" scoped>
@import "~@core/scss/base/pages/app-invoice.scss";
@import '~@core/scss/base/components/variables-dark';

.form-item-section {
background-color:$product-details-bg;
}

.form-item-action-col {
  width: 27px;
}

.repeater-form {
  // overflow: hidden;
  transition: .35s height;
}

.v-select {
  &.item-selector-title,
  &.payment-selector {
    background-color: #fff;

    .dark-layout & {
      background-color: unset;
    }
  }
}

.dark-layout {
  .form-item-section {
    background-color: $theme-dark-body-bg;

    .row .border {
      background-color: $theme-dark-card-bg;
    }

  }
}
</style>
